/*------------------------------About Page----------------*/
.about-content{
  padding: 50px 0;
  max-width: 900px;
  margin-left: auto;
  margin-right: auto;
}
.about-content .dark-title{
	text-align: center;
}
.about-content p, .about-content li{
	font-size: 15px;
  line-height: 1.45;
  color: #86878b;
}
.about-content ul{
	list-style: disc;
	padding-left: 20px;
	margin-bottom: 20px;
}
/*---------------------------Wait Page--------------------*/
.wait-page{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50vh;
  flex-direction: column;
}
.wait-page i{
  font-size: 40px;
  -webkit-animation: spin 1.5s ease-in-out infinite forwards;
  animation: spin 1.5s ease-in-out infinite forwards;
  margin-bottom: 20px;
}
/*-------------------------Animation------------------*/
@-moz-keyframes spin {
    from { -moz-transform: rotate(0deg); }
    to { -moz-transform: rotate(360deg); }
}
@-webkit-keyframes spin {
    from { -webkit-transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); }
}
@keyframes spin {
    from {transform:rotate(0deg);}
    to {transform:rotate(360deg);}
}
