/*---------------------------Upper Header-------------*/
.nav-link{
  font-size: 14px;
  font-weight: bold;
  text-align: center;
  color: #e5e5e5;
  text-transform: uppercase;
  margin-right: 5px;
  padding: 10px 15px!important;
  -webkit-transition: ease .2s;
  transition: ease .2s;
  cursor: pointer;
}
.nav-link:hover, .nav-link:focus{
  background-color: #cc2027;
  color: #fff;
}
.navbar{
  padding: 0;
  background-color: #222222;
  min-height: 41px;
}
.navbar-brand img{
  width: 200px;
}
.active{
  background-color: #cc2027!important;
}
.navbar-collapse{
  justify-content: center;
}
/*---------------------------Clock-------------*/
.time-clock{
  position: absolute;
  top: 10px;
  left: 0;
  display: flex;
  align-items: center;
}
.current-time{
	font-size: 14px;
  font-weight: 600;
  color: #e5e5e5;
  margin: 0 5px;
}
.fa-clock{
	color: #e5e5e5;
	margin-left: 20px;
	font-size: 18px;
}
.time-clock span{
	color: #e5e5e5;
	font-size: 14px;
	font-weight: 600;
}
/*---------------------------Lang bar-------------*/
.lang-bar{
  position: absolute;
  right: 0px;
  top: 8px;
  z-index: 99;
}
.lang-bar span{
	background-color: #7b0005;
  padding: 10px;
  color: #fff;
  margin-right: 3px;
  cursor: pointer;
  border-radius: 0px;
  -webkit-transition: ease .2s;
  transition: ease .2s;
}
.lang-bar span:last-of-type{
  margin: 0;
}
.lang-bar span:hover{
	background-color: #cc2027;
}
/*-------------------------Slider--------------------*/
.slide img{
  width: 100%;
}
.slider-overlay img{
  width: 280px;
  margin: 13vh 0 4vh;
}
